.container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 40px 0;
    margin: 0 auto;
    width: 60%;
}

.card {
    border: none;
}

.card img {
    height: 170px;
    width: 170px;
    margin: 8px 25px;
    border: 5px solid #f1f1f1;
    border-radius: 3px;
    box-shadow: 0 3px 6px rgba(0,0,0,.16), 0 3px 6px rgba(0,0,0,.23);
    cursor: pointer;
}